.form{
    display: flex;
    flex-direction: column;
    &__input{
        border: 2px solid rgba(#ddd, .7);
        border-radius: 4px;
        height: 3.5rem;
        outline: none;
        padding: 1rem;
        margin-bottom: $s-size;
    }

    &__button{
        display: block;
        width: 100%;
        border-radius: 5px;
        font-size: $m-size;
        color: #fff;
        background-color: $primary;
        padding: 1rem;
        font-size: 2rem;
        margin-bottom: $s-size;
        outline: none;
    }
}

