.todo-container{
}

.todo{
    display: flex;
    justify-content: space-between;
    margin-bottom: $s-size;
    align-items: center;

    
    &__input{
        display: inline-block;
        flex: 10;
        border: none;
        outline: none;
        height: 100%;
        background-color: #fff;
    }

    &__input:focus{
        border-bottom: 1px solid rgba(#aaa, .5);
    }

    &__checkbox{
        margin-right: $s-size;
        background: red;
    }

    &__checkbox:checked + input{
        text-decoration: line-through;
    }

    &__icons{
        flex: 0;
        margin-left: $s-size;
        display: flex;
        align-items: center;
        justify-content: center;

        &-edit{
            color: $primary;
            font-size: 2rem;
            margin-right: $s-size;
            cursor: pointer;
        }

        &-delete{
            color: #c70000;
            font-size: 2rem;
            cursor: pointer;
        }
    }

}